/*content containers*/
header {
  z-index: 2;
  width: 100%;
  padding: 0px 5%;
  border-bottom: 2px solid rgba(27, 27, 27, 0.85);
  background-color: rgba(27, 27, 27, 0.85);
  position: fixed;
  top: 0px; }

#headerNav {
  width: 90%;
  margin: auto; }

body {
  font-family: "Raleway", "Georgia", "serif";
  color: white;
  text-shadow: 2px 2px 5px black;
  background-color: #252525; }

main {
  z-index: 1;
  position: relative;
  width: 95%;
  margin: 150px auto 0px auto;
  padding: 2.5%; }

section {
  margin: auto;
  margin-top: 30px;
  border-bottom: 2px solid #CFA815; }

aside {
  width: 100%; }
  @media (min-width: 1200px) {
    aside {
      width: 75%;
      margin-left: 15%; } }

button {
  cursor: pointer; }
#availability .block-quote {
  width: 100%;
  display: block;
  margin: auto; }
  @media (min-width: 1200px) {
    #availability .block-quote {
      width: 550px;
      margin: -175px 4%;
      float: right; } }

@media (min-width: 750px) {
  .wrapper #services,
  .wrapper #references,
  .wrapper #portfolio,
  .wrapper #contact {
    border-bottom: none;
    display: inline-grid;
    width: 50%; } }

/**************************/
/*        Reusables       */
/**************************/
::-webkit-scrollbar{
    width: 10px;
}
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0);
    background: rgba(0,0,0,0);
    border-radius: 10px;
}
::-webkit-scrollbar-track-piece:disabled {
  display: none !important;
}
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: black;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,1);
}
::-webkit-scrollbar-track:hover{
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,1);
}
#name {
  text-align: center;
  width: 90%;
  display: block;
  margin: 30px auto; }

.mobile {
  display: block;
  margin: auto; }
  @media (min-width: 750px) {
    .mobile {
      display: none; } }

.hidden {
  display: none; }
  @media (min-width: 750px) {
    .hidden {
      display: block; } }

.widget {
  width: 250px;
  padding: 10px;
  border: 1px solid #a2009d;
  display: block;
  margin: auto;
  margin-bottom: 30px;
  text-align: center; }

.close {
  color: red;
  position: absolute;
  top: 10%;
  right: 10%;
  font-size: 2rem;
  display: none;
  cursor: pointer; }

#contact-block {
  width: 280px;
  display: block;
  margin: auto; }

hr {
  border-color: #CFA815;
  border-width: 1.5px; }

.calendar {
  margin: auto; }
  @media (min-width: 1200px) {
    .calendar {
      margin: 30px 10%; } }

/**************************/
/*         modal          */
/**************************/
.port-item.modal {
  z-index: 1000;
  position: fixed;
  top: -23px;
  left: -130px;
  width: 108vw;
  height: 106vh;
  background-color: rgba(0, 0, 0, 0.87);
  overflow: hidden;
  cursor: default; }
  @media (min-width: 750px) {
    .port-item.modal {
      display: block;
      overflow: visible; } }
  @media (min-width: 1200px) {
    .port-item.modal {
      left: -85px;
      top: -37px; } }
  .port-item.modal img {
    display: block;
    width: 65%;
    margin: auto; }
  .port-item.modal h4 {
    display: block;
    width: 65%;
    margin: 30px auto 0px auto;
    text-align: center; }
  .port-item.modal .mobile {
    display: block; }
  .port-item.modal .container {
    margin: 0px auto;
    width: 65%; }
  .port-item.modal .close {
    display: block; }

/**************************/
/*       TYPOGRAPHY       */
/**************************/
footer a {
  display: inline-block; }

h1, h3 {
  display: inline-block;
  font-family: "Orbitron", "Courier New", "monospace", "sans-serif";
  text-align: center;
  font-size: 2em;
  margin: 5px;
  color: #a2009d;
  text-shadow: 2px 2px 5px black; }

h3, section a {
  display: block;
  font-size: 1.5em; }

h4 {
  font-family: "Raleway", "Georgia", "serif";
  font-size: 1em;
  color: white;
  margin: auto;
  text-shadow: 2px 2px 5px black;
  display: block;
  text-align: center;}

a {
  text-decoration: none;
  font-family: "Raleway", "Georgia", "serif";
  font-size: 1.2em;
  color: white;
  text-shadow: 2px 2px 5px #0b0b0b;
  padding: 5px;
  cursor: pointer; }

section a {
  text-align: center; }

a:hover, .active {
  color: #0CEED0;
  text-shadow: 2px 2px 2px #000; }

.port-item a {
  font-size: 1.25rem;
  font-family: "Orbitron", "Courier New", "monospace", "sans-serif";
  text-shadow: 2px 2px 5px black; }

.port-item ul {
  padding: 0px;
  width: 90%;
  margin: auto; }

.port-item p {
  font-size: 1.2rem; }

.port-item li {
  font-size: 1rem;
  border-left: 3px solid #CFA815;
  padding: 2px 0px 2px 15px;
  line-height: 15px; }

.port-item li, .port-item p {
  width: 100%;
  margin: auto;
  list-style: none;
  text-align: left;
  font-family: "Raleway", "Georgia", "serif";
  color: white; }

section p {
  font-size: 1.25em;
  width: 95%;
  margin: 2.5%;
  padding: 5px;
  text-shadow: 2px 2px 5px black; }

aside p {
  border-left: 3px solid #CFA815;
  min-height: 100px;}

span {
  color: #0CEED0;
  font-style: italic; }



@media (min-width: 750px) {
  #availability .block-quote p {
    width: 100%; } }
@media (min-width: 1200px) {
  #availability .block-quote p {
    width: 500px; } }

@media (min-width: 750px) {
  .block-quote {
    width: 25%;
    min-width: 200px;
    display: inline-block;
    margin-left: 2.5%; }
    .block-quote p {
      width: 183px; }
    .block-quote h3 {
      text-align: left;
      width: 50px;
      margin-left: 30px; } }
@media (min-width: 1200px) {
  .block-quote {
    margin-left: 7%; } }

/**************************/
/*        Soc Med         */
/**************************/
svg {
  display: block;
  margin: auto;
  width: 50px; }

.outer-shape {
  opacity: 1;
  fill: black; }

.inner-shape {
  opacity: 1;
  fill: white; }

.port-item {
  width: 98%;
  display: inline-grid;
  margin: 1% 0%;
  cursor: pointer; }
.port-item > .container{
  border:15px solid rgba(27, 27, 27, 1);
  padding:15px;}
  @media (min-width: 750px) {
    .port-item {
      width: 80%;
      margin-left: 10%; } }
  @media (min-width: 1200px) {
    .port-item {
      width: 30%;
      margin-left: 2%; } }
  .port-item img {
    width: 100%; }
    @media (min-width: 750px) {
      .port-item img {
        min-height: 195px;
        max-height: 500px; } }

/**************************/
/*       NAVIGATION       */
/**************************/
nav {
  width: 100%;
  display: block;
  margin: auto;
  margin-top: 15px;
  text-align: center; }

nav a {
  display: inline-table;
  width: 95px;
  margin: 0px 20px;
  height: 25px; }


header svg {
  display: inline-block;
  float: left;
  margin-top: 8px;
  margin-right: -31px;
  width: 28px; }
  @media (min-width: 1200px) {
    header svg {
      display: none; } }

nav h4 {
  font-size: 1.5em;
  padding: 12px;
  margin-left: 34%;
  display: block;
  width: 30%; }
  @media (min-width: 1200px) {
    nav h4 {
      display: none; } }



